.page{position:relative;z-index:2;background:#fff;min-height:100vh;padding-bottom:4rem}.container{max-width:1400px;margin:0 auto;padding:8rem 3rem 0}.header{margin-bottom:4rem}.title{font-size:2.5rem;font-weight:300;letter-spacing:-0.02em;color:#000;margin:0 0 1rem;line-height:1.1}.subtitle{font-size:1rem;color:#666;margin:0;line-height:1.5}.archiveGrid{display:grid;grid-template-columns:repeat(auto-fit, minmax(320px, 1fr));gap:2rem;margin-bottom:4rem}.archiveItem{position:relative}.itemLink{display:block;text-decoration:none;color:inherit;transition:all .3s ease}.itemLink:hover .image{transform:scale(1.02)}.itemLink:hover .itemTitle{color:#666}.imageContainer{position:relative;overflow:hidden;margin-bottom:1rem;aspect-ratio:3/2}.image{width:100%;height:100%;object-fit:cover;transition:transform .3s ease}.itemContent{padding:0}.itemType{display:inline-block;font-size:.75rem;font-weight:600;text-transform:uppercase;letter-spacing:.1em;color:#999;margin-bottom:.5rem}.itemType::after{content:"";display:inline-block;width:4px;height:4px;border-radius:50%;background:#999;margin-left:.5rem;vertical-align:middle}.itemTitle{font-size:1.1rem;font-weight:500;color:#000;margin:0 0 .5rem;line-height:1.3;transition:color .3s ease}.itemDate{display:block;font-size:.85rem;color:#999;margin-bottom:.75rem;font-weight:400}.itemDescription{font-size:.9rem;line-height:1.5;color:#666;margin:0;display:-webkit-box;-webkit-line-clamp:3;-webkit-box-orient:vertical;overflow:hidden}@media(max-width: 1024px){.container{padding:7rem 2rem 0}.archiveGrid{grid-template-columns:repeat(auto-fit, minmax(280px, 1fr));gap:1.5rem}}@media(max-width: 768px){.container{padding:6rem 1.5rem 0}.title{font-size:2rem}.header{margin-bottom:3rem}.archiveGrid{grid-template-columns:1fr;gap:2rem}.archiveItem .itemLink{display:grid;grid-template-columns:120px 1fr;gap:1rem;align-items:start}.archiveItem .imageContainer{margin-bottom:0;aspect-ratio:1}.archiveItem .itemContent{padding:0}.archiveItem .itemTitle{font-size:1rem}.archiveItem .itemDescription{-webkit-line-clamp:2}}@media(max-width: 480px){.container{padding:5rem 1rem 0}.title{font-size:1.75rem}.archiveItem .itemLink{grid-template-columns:100px 1fr;gap:.75rem}.archiveItem .itemTitle{font-size:.95rem}.archiveItem .itemDescription{font-size:.85rem}}.archiveItem:first-child{grid-column:span 2}.archiveItem:first-child .itemLink{display:grid;grid-template-columns:1fr 1fr;gap:2rem;align-items:center}.archiveItem:first-child .imageContainer{aspect-ratio:4/3}.archiveItem:first-child .itemTitle{font-size:1.5rem;margin-bottom:1rem}.archiveItem:first-child .itemDescription{font-size:1rem;-webkit-line-clamp:4}@media(max-width: 1024px){.archiveItem:first-child{grid-column:span 1}.archiveItem:first-child .itemLink{display:block}.archiveItem:first-child .imageContainer{margin-bottom:1rem;aspect-ratio:3/2}.archiveItem:first-child .itemTitle{font-size:1.2rem;margin-bottom:.5rem}.archiveItem:first-child .itemDescription{font-size:.9rem;-webkit-line-clamp:3}}